.header {
  grid-area: header;
}
.header .logo {
  display: block;
  margin-left: 20vw;
  max-height: 60px;
  max-width: 100%;
}
.header .menu_btn {
  display: none;
  position: fixed;
  z-index: 1;
  top: 5px;
  right: 5px;
  padding: 7px;
  border-radius: 0;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 40px;
  background-color: rgba(0, 0, 0, 0.8);
}

@media (max-width: 768px) {
  .header .menu_btn {
    display: block;
  }
}
@media (max-width: 555px) {
  .header .logo {
    margin: 0;
    max-width: 70%;
  }
}